.register {
  height: calc(100vh - 60px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /*  linear-gradient(
      rgba(255, 255, 255, 0.2),
      rgba(255, 255, 255, 0.2)
    ), */
  background: url('../../components/assets/pencils.jpg');
  background-size: cover;
}

.registerTitle {
  font-size: 50px;
  font-family: 'Roboto', serif;
}

.registerForm {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.registerForm > label {
  margin: 10px 0;
  font-family: 'Roboto', serif;
  align-self: center;
  font-weight: bolder;
}

.registerInput:focus {
  outline: none;
}

.registerInput {
  padding: 10px;
  background-color: #fff;
  border: none;
  border-radius: 5px;
  color: gray;
}

.registerInput::placeholder {
  color: rgb(182, 179, 179);
}

.registerButton {
  margin-top: 20px;
  cursor: pointer;
  background-color: rgb(0, 183, 255);
  border: none;
  color: white;
  border-radius: 10px;
  padding: 10px 0;
}

.registerLoginButton {
  position: absolute;
  cursor: pointer;
  top: 80px;
  right: 20px;
  border: none;
  background-color: white;
  font-family: 'Roboto', serif;
  font-weight: bolder;
  border-radius: 10px;
  padding: 10px 20px;
}

.errorMessage {
  margin: 10px;
  font-family: 'Roboto', serif;
  font-weight: bolder;
}
