.login {
  height: calc(100vh - 60px);
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /*  linear-gradient(
      rgba(255, 255, 255, 0.2),
      rgba(255, 255, 255, 0.2)
    ), */
  background: url('../../components/assets/pencils.jpg');
  background-size: cover;
}

.loginTitle {
  font-size: 50px;
  font-family: 'Roboto', serif;
}

.loginForm {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.loginForm > label {
  margin: 10px 0;
  font-family: 'Roboto', serif;
  align-self: center;
  font-weight: bolder;
}

.loginInput:focus {
  outline: none;
}

.loginInput {
  padding: 10px;
  background-color: #fff;
  border: none;
  border-radius: 5px;
  color: gray;
}

.loginInput::placeholder {
  color: rgb(182, 179, 179);
}

.loginButton {
  margin-top: 20px;
  cursor: pointer;
  background-color: rgb(0, 183, 255);
  border: none;
  color: white;
  border-radius: 10px;
  padding: 10px 0;
}

.loginButton:disabled {
  cursor: not-allowed;
  background-color: rgb(137, 221, 255);
}

.loginRegisterButton {
  position: absolute;
  cursor: pointer;
  top: 80px;
  right: 20px;
  border: none;
  background-color: white;
  font-family: 'Roboto', serif;
  font-weight: bolder;
  border-radius: 10px;
  padding: 10px 20px;
}
